*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: $fz-default;
  line-height: 24px;
  font-family: $ff-roboto-condensed;
  color: $color-sapphire;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
