  text-rendering: optimizeLegibility;
  // ÐµÑÐ»Ð¸ Ð¿Ð¾ Ð¿ÑÐµÐ¶Ð½ÐµÐ¼Ñ ÐµÑÑÑ Ð¿ÑÐ¾Ð±Ð»ÐµÐ¼Ñ Ñ Ð²ÑÑÐ°Ð²Ð½Ð¸Ð²Ð°Ð½Ð¸ÐµÐ¼
