}

body {
  width: 100%;
  height: 100%;

  background-color: $color-white;
}

a {
  color: $color-sapphire;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

