// rgba Ð½Ðµ Ð¿Ð¾Ð´Ð¾Ð¹Ð´ÐµÑ, ÑÐºÐ¾Ð½Ð²ÐµÑÑÐ¸ÑÑÐ¹ÑÐµ ÑÐ²ÐµÑ Ð² hex Ð±ÐµÐ· Ð¿ÑÐ¾Ð·ÑÐ°ÑÐ½Ð¾ÑÑÐ¸
// ÐµÑÐ»Ð¸ Ð² ÑÑÐ¸Ð»ÑÑ ÑÐ¶Ðµ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·ÑÐµÑÑÑ box-shadow ÐµÑÑÑ Ð´ÑÑÐ³Ð¾Ðµ ÑÐµÑÐµÐ½Ð¸Ðµ -
// Ð·Ð°Ð´Ð°ÑÑ Ðº ÑÐ¿Ð¸ÑÐºÑ ÑÑÐ°Ð½Ð·Ð¸ÑÐµÐ½Ð¾Ð² `background-color 10000000s ease-out`
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px $color-white;

  -webkit-text-fill-color: $color-sapphire;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// ie11 X removal
input {
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}
